console.log("Executando...");

const buttonMais = document.querySelector('.btn-success');
const buttonMenos = document.querySelector('.btn-danger');


buttonMais.addEventListener('click', ()=> soma(+1));
buttonMenos.addEventListener('click', ()=> soma(-1));

const output = document.querySelector('output');

/**
 * Computa a contagem atual e re-renderiza ent√£o atualiza o elemento `<output>`
 * 
 * @param {number} valor Valor a ser acrescido da contagem atual; pode ser negativo.
*/

function soma(valor) {

    //computa a contagem atual:
    const valorAtual = +output.innerHTML + valor; // parte logica
    output.innerHTML = valorAtual;

    // adiciona ou remove classes dependendo da contagem atual:
    if(valorAtual > 0){
        output.classList.add('text-success');
    } else if (valorAtual < 0){
        output.classList.add('text-danger');
    }else{
        output.classList.remove('text-success');
        output.classList.remove('text-danger');
    }
}
